import React from "react"
import { graphql } from 'gatsby'
import Layout from "../components/layout"

import Container from 'muicss/lib/react/container';
import Row from 'muicss/lib/react/row';
import Col from 'muicss/lib/react/col';
import Divider from 'muicss/lib/react/divider';
import Select from 'muicss/lib/react/select';
import Option from 'muicss/lib/react/option';

import ClgTitle from '../components/clgtitle'
import ClgMenu from '../components/clgmenu'

import ClgTicket from '../components/clgticket'

function onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
}

class ClgTicketsTemplate extends React.Component {

  state = {
    filtre_domaine: "",
    filtre_affectation: ""
  };

  onChange_domaine(ev) {
  this.setState({filtre_domaine: ev.target.value,filtre_affectation:""});
  }

  onChange_affectation(ev) {
  this.setState({filtre_affectation: ev.target.value,filtre_domaine:""});
  }

  ticket_filter(node) {
    if(this.state.filtre_domaine === "" && this.state.filtre_affectation === "" ){
      return true
    }
    var a_a = [node.Produit__Produit,node.Service__Nom,node.Sous_cat_gorie_de_service__Nom]
    var b_b = [node.Agent__Nom_complet,node.Equipe__Nom]
    return  a_a.includes(this.state.filtre_domaine) || b_b.includes(this.state.filtre_affectation)
  }

  render() {


    const info = this.props.data.clgListeXlsxRefClg
    const tickets = this.props.data.allExportDeDemandeUtilisateurXlsxSheet1.edges
    const nbtickets = tickets.length
    const liste_produits = tickets.map(edge => edge.node.Produit__Produit)
    const liste_services = tickets.map(edge => edge.node.Service__Nom)
    const liste_sscat = tickets.map(edge => edge.Sous_cat_gorie_de_service__Nom)
    const filtre_a = liste_produits.concat(liste_services.concat(liste_sscat))

    const liste_equipes = tickets.map(edge => edge.node.Equipe__Nom)
    const liste_agents = tickets.map(edge => edge.node.Agent__Nom_complet)
    const filtre_b = liste_equipes.concat(liste_agents)

    return (<Layout>
      <ClgMenu id={info.rne} />
      <ClgTitle info={info}  />
      <Divider/>
        <Container fluid={true} style={{marginTop:'22px'}}>
          <Row>
            <Col md="12" className="mui--text-title">TICKETS ({nbtickets})</Col>
          </Row>
          <Row>
            <Col md="6" >
            <Select name='input' value={this.state.filtre_domaine} placeholder='Domaines' onChange={this.onChange_domaine.bind(this)}>
            {filtre_a.filter(onlyUnique).map(function(item, i){
              var nb=filtre_a.filter( x=> x==item).length
                return (
                    <Option value={item} label={item+" ("+nb+")"} key={i} className="mui--text-caption"/>
                )})}
            </Select>
            </Col>
            <Col md="6" >
            <Select name='input' value={this.state.filtre_affectation} placeholder='Affectation' onChange={this.onChange_affectation.bind(this)}>
            {filtre_b.filter(onlyUnique).map(function(item, i){
              var nb=filtre_b.filter( x=> x==item).length
                return (
                    <Option value={item} label={item+" ("+nb+")"} key={i} className="mui--text-caption"/>
                )})}
            </Select>
            </Col>
          </Row>
          {
            tickets.map(function(edge, i){
             if(this.ticket_filter(edge.node)){
              return (
              <Row key={i}>
                <Col md="12">
                   <ClgTicket node={edge.node} />
                </Col>
              </Row>)
            }else {return}
          },this)}
      </Container>

    </Layout>)
  }
}

export default ClgTicketsTemplate

export const pageQuery = graphql `
    query($rne: String!) {
      allExportDeDemandeUtilisateurXlsxSheet1(filter: {Etablissement_concern___Num_ro_UAI_d__tablissement: {eq: $rne}}, sort: {fields: Derni_re_mise___jour, order: DESC}) {
          edges {
            node {
              Agent__Nom_complet
              Date_de_d_but(formatString: "dddd DD/MM/YY", locale: "fr")
              Demandeur__Nom_complet
              Derni_re_mise___jour(formatString: "dddd DD/MM/YY", locale: "fr")
              Description
              Equipe__Nom
              Etablissement_concern___Nom_complet
              Etablissement_concern___Num_ro_UAI_d__tablissement
              Etablissement_concern___T_l_phone
              Famille_de_produit__Nom
              N__d_inventaire
              Nombre_de_relances
              Objet_de_la_demande
              Priorit_
              Produit__Produit
              R_f_rence
              Service__Nom
              Sous_cat_gorie_de_service__Nom
              Statut
              T_l_phone_de_rappel
              Urgence
            }
          }
        }

  clgListeXlsxRefClg(rne: {eq: $rne}) {
  abreviation
  ancien_nom
  canton
  categorie
  date_pnc(formatString: "DD/MM/YY")
  email_college
  email_gestionnaire
  etat_pnc
  info_ac_nantes
  nom
  numid
  rne
  tel_college
  titre
  vague_pnc
  ville
  }
}
  `
